html {
  position: relative; }

body {
  background-color: black;
  font-family: 'Open Sans';
  color: wheat;
  height: 5000px; }

.bg-dark {
  background-color: rgba(0, 0, 0, 0.5) !important; }
  .bg-dark .navbar-nav .nav-link {
    color: rgba(255, 255, 255, 0.9); }
    .bg-dark .navbar-nav .nav-link.active {
      color: rgba(255, 0, 0, 0.9); }
  .bg-dark .navbar-brand {
    font-weight: bold; }
    .bg-dark .navbar-brand span {
      color: red; }

.banner .swiper-container {
  width: 100%;
  height: 100%;
  background: #000;
  height: 100vh; }
  .banner .swiper-container .parallax-bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 130%;
    height: 100%;
    background-size: cover;
    background-position: center; }
  .banner .swiper-container .banner-cont {
    position: absolute;
    z-index: 2;
    top: 40%;
    left: 25%; }
    .banner .swiper-container .banner-cont h2 {
      font-size: 4em;
      margin-bottom: 3px;
      font-weight: bold;
      animation: text-flicker-out-glow 2.5s linear infinite both; }
    .banner .swiper-container .banner-cont span {
      position: absolute;
      content: '';
      height: 3px;
      width: 80%;
      background-color: red; }
    .banner .swiper-container .banner-cont h3 {
      letter-spacing: 5px;
      margin-top: 5px;
      font-size: 2em; }
  .banner .swiper-container .seemore {
    position: absolute;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.6);
    font-size: 1.5em;
    font-weight: bold;
    bottom: 40px;
    left: 45%;
    z-index: 2;
    cursor: pointer;
    animation: bounce-top 2s ease-in-out infinite both; }
    .banner .swiper-container .seemore i {
      position: absolute;
      bottom: -20px;
      left: 50%; }
  .banner .swiper-container .swiper-wrapper .swiper-slide {
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;
    box-sizing: border-box;
    padding: 30vh 20vw; }

.fotter {
  width: 100%;
  height: 50px; }
  .fotter .copyright {
    color: 0; }

